.card {
  border: 1px solid #f3f3f3;
  padding: 30px;
  background: #ffffff;
  border-radius: 40px;
  width: 210px;
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
  margin-bottom: 40px;

  &:hover {
    box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
    transform: translateY(-5px);
  }

  h5 {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
  }

  span {
    font-weight: 500;
    font-size: 11px;
    line-height: 13px;
    text-transform: uppercase;
    color: #bdbdbd;
  }

  b {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
  }
}